'use client'

import { useState, useEffect } from 'react'

interface Guild {
  id: string
  name: string
  icon: string | null
  owner: boolean
  permissions: string
}

interface Channel {
  id: string
  name: string
  type: number
  guild_id?: string
}

interface Message {
  id: string
  content: string
  timestamp: string
  author: {
    id: string
    username: string
    avatar: string | null
  }
}

export default function LimparServidorPage() {
  const [guilds, setGuilds] = useState<Guild[]>([])
  const [channels, setChannels] = useState<Channel[]>([])
  const [messages, setMessages] = useState<Message[]>([])
  const [selectedGuild, setSelectedGuild] = useState('')
  const [selectedChannel, setSelectedChannel] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isLoadingGuilds, setIsLoadingGuilds] = useState(false)
  const [isLoadingChannels, setIsLoadingChannels] = useState(false)
  const [isLoadingMessages, setIsLoadingMessages] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [hasToken, setHasToken] = useState(false)
  const [deleteCount, setDeleteCount] = useState(0)
  const [showConfirmation, setShowConfirmation] = useState(false)

  useEffect(() => {
    const token = localStorage.getItem('discordToken')
    setHasToken(!!token)
    if (token) {
      loadGuilds()
    }
  }, [])

  const loadGuilds = async () => {
    setIsLoadingGuilds(true)
    setError('')
    
    try {
      const token = localStorage.getItem('discordToken')
      if (!token) {
        setError('Token não encontrado')
        return
      }

      const response = await fetch('https://discord.com/api/v10/users/@me/guilds', {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`Erro ${response.status}: ${response.statusText}`)
      }

      const data = await response.json()
      setGuilds(data)
    } catch (err: any) {
      setError(`Erro ao carregar servidores: ${err.message}`)
    } finally {
      setIsLoadingGuilds(false)
    }
  }

  const loadChannels = async (guildId: string) => {
    setIsLoadingChannels(true)
    setError('')
    setChannels([])
    setMessages([])
    setSelectedChannel('')
    
    try {
      const token = localStorage.getItem('discordToken')
      if (!token) {
        setError('Token não encontrado')
        return
      }

      const response = await fetch(`https://discord.com/api/v10/guilds/${guildId}/channels`, {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`Erro ${response.status}: ${response.statusText}`)
      }

      const data = await response.json()
      // Filtrar apenas canais de texto (tipo 0)
      const textChannels = data.filter((channel: Channel) => channel.type === 0)
      setChannels(textChannels)
    } catch (err: any) {
      setError(`Erro ao carregar canais: ${err.message}`)
    } finally {
      setIsLoadingChannels(false)
    }
  }

  const loadMessages = async (channelId: string, limit: number = 50) => {
    setIsLoadingMessages(true)
    setError('')
    
    try {
      const token = localStorage.getItem('discordToken')
      if (!token) {
        setError('Token não encontrado')
        return
      }

      const response = await fetch(`https://discord.com/api/v10/channels/${channelId}/messages?limit=${limit}`, {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`Erro ${response.status}: ${response.statusText}`)
      }

      const data = await response.json()
      // Filtrar apenas mensagens do usuário atual
      const userToken = localStorage.getItem('discordToken')
      const userResponse = await fetch('https://discord.com/api/v10/users/@me', {
        headers: {
          'Authorization': userToken!,
          'Content-Type': 'application/json'
        }
      })
      
      if (userResponse.ok) {
        const userData = await userResponse.json()
        const userMessages = data.filter((msg: Message) => msg.author.id === userData.id)
        setMessages(userMessages)
      } else {
        setMessages(data)
      }
    } catch (err: any) {
      setError(`Erro ao carregar mensagens: ${err.message}`)
    } finally {
      setIsLoadingMessages(false)
    }
  }

  const deleteMessage = async (channelId: string, messageId: string) => {
    const token = localStorage.getItem('discordToken')
    if (!token) {
      throw new Error('Token não encontrado')
    }

    const response = await fetch(`https://discord.com/api/v10/channels/${channelId}/messages/${messageId}`, {
      method: 'DELETE',
      headers: {
        'Authorization': token,
        'Content-Type': 'application/json'
      }
    })

    if (!response.ok) {
      throw new Error(`Erro ${response.status}: ${response.statusText}`)
    }
  }

  const handleDeleteAllMessages = async () => {
    if (!selectedChannel || messages.length === 0) {
      setError('Selecione um canal e carregue as mensagens primeiro')
      return
    }

    setIsLoading(true)
    setError('')
    setSuccess('')
    setDeleteCount(0)

    try {
      let deletedCount = 0
      let errorCount = 0

      for (const message of messages) {
        try {
          await deleteMessage(selectedChannel, message.id)
          deletedCount++
          setDeleteCount(deletedCount)
          
          // Delay entre exclusões para evitar rate limit
          await new Promise(resolve => setTimeout(resolve, 1500))
        } catch (err) {
          errorCount++
          console.error(`Erro ao deletar mensagem ${message.id}:`, err)
        }
      }

      setSuccess(`${deletedCount} mensagem(s) deletada(s) com sucesso!${errorCount > 0 ? ` ${errorCount} erro(s) ocorreram.` : ''}`)
      setShowConfirmation(false)
      
      // Recarregar mensagens
      await loadMessages(selectedChannel)
    } catch (err: any) {
      setError(`Erro ao deletar mensagens: ${err.message}`)
    } finally {
      setIsLoading(false)
      setDeleteCount(0)
    }
  }

  const getGuildIcon = (guild: Guild) => {
    if (guild.icon) {
      return `https://cdn.discordapp.com/icons/${guild.id}/${guild.icon}.png?size=64`
    }
    return `https://placehold.co/64x64?text=${guild.name.charAt(0)}`
  }

  const formatDate = (timestamp: string) => {
    return new Date(timestamp).toLocaleString('pt-BR')
  }

  if (!hasToken) {
    return (
      <div className="max-w-2xl mx-auto text-center space-y-8 animate-fade-in">
        <div className="space-y-4">
          <div className="w-20 h-20 bg-gradient-to-r from-red-500 to-red-600 rounded-full flex items-center justify-center mx-auto">
            <span className="text-3xl">🔒</span>
          </div>
          <h1 className="text-3xl font-bold text-white">Token Não Configurado</h1>
          <p className="text-gray-300 text-lg">
            Configure seu token Discord para usar a ferramenta de limpeza de servidor.
          </p>
        </div>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <a 
            href="/config" 
            className="px-8 py-4 bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-500 hover:to-purple-600 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Configurar Token
          </a>
          <a 
            href="/tutorial" 
            className="px-8 py-4 border border-purple-600 hover:bg-purple-600/10 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Ver Tutorial
          </a>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto space-y-8 animate-fade-in">
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold bg-gradient-to-r from-orange-400 to-orange-600 bg-clip-text text-transparent">
          Limpar Mensagens de Servidor
        </h1>
        <p className="text-gray-300 text-lg">
          Delete suas mensagens de canais específicos em servidores Discord.
        </p>
      </div>

      {/* Seleção de Servidor */}
      <div className="bg-gray-900/50 border border-orange-800/30 rounded-xl p-6 space-y-4">
        <h2 className="text-xl font-bold text-white">Selecionar Servidor</h2>
        
        <div className="flex gap-4">
          <select
            value={selectedGuild}
            onChange={(e) => {
              setSelectedGuild(e.target.value)
              if (e.target.value) {
                loadChannels(e.target.value)
              }
            }}
            disabled={isLoadingGuilds}
            className="flex-1 p-3 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-orange-500 focus:ring-2 focus:ring-orange-500/20 transition-all duration-300 text-white"
          >
            <option value="">Selecione um servidor...</option>
            {guilds.map(guild => (
              <option key={guild.id} value={guild.id}>
                {guild.name}
              </option>
            ))}
          </select>
          
          <button
            onClick={loadGuilds}
            disabled={isLoadingGuilds}
            className="px-4 py-2 bg-blue-600 hover:bg-blue-500 disabled:bg-gray-600 rounded-lg font-medium transition-all duration-300 transform hover:scale-105 disabled:scale-100"
          >
            {isLoadingGuilds ? 'Carregando...' : 'Atualizar'}
          </button>
        </div>

        {selectedGuild && (
          <div className="flex items-center space-x-3 p-3 bg-gray-800/30 rounded-lg">
            <img
              src={getGuildIcon(guilds.find(g => g.id === selectedGuild)!)}
              alt="Server icon"
              className="w-8 h-8 rounded-full"
            />
            <span className="text-white font-medium">
              {guilds.find(g => g.id === selectedGuild)?.name}
            </span>
          </div>
        )}
      </div>

      {/* Seleção de Canal */}
      {selectedGuild && (
        <div className="bg-gray-900/50 border border-orange-800/30 rounded-xl p-6 space-y-4">
          <h2 className="text-xl font-bold text-white">Selecionar Canal</h2>
          
          <select
            value={selectedChannel}
            onChange={(e) => {
              setSelectedChannel(e.target.value)
              if (e.target.value) {
                loadMessages(e.target.value)
              }
            }}
            disabled={isLoadingChannels}
            className="w-full p-3 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-orange-500 focus:ring-2 focus:ring-orange-500/20 transition-all duration-300 text-white"
          >
            <option value="">Selecione um canal...</option>
            {channels.map(channel => (
              <option key={channel.id} value={channel.id}>
                # {channel.name}
              </option>
            ))}
          </select>

          {isLoadingChannels && (
            <div className="text-center py-4">
              <div className="loading-spinner mx-auto mb-2"></div>
              <p className="text-gray-400">Carregando canais...</p>
            </div>
          )}
        </div>
      )}

      {/* Mensagens */}
      {selectedChannel && (
        <div className="bg-gray-900/50 border border-orange-800/30 rounded-xl p-6 space-y-4">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-bold text-white">Suas Mensagens</h2>
            <div className="flex gap-2">
              <button
                onClick={() => loadMessages(selectedChannel)}
                disabled={isLoadingMessages}
                className="px-4 py-2 bg-blue-600 hover:bg-blue-500 disabled:bg-gray-600 rounded-lg font-medium transition-all duration-300 transform hover:scale-105 disabled:scale-100"
              >
                {isLoadingMessages ? 'Carregando...' : 'Atualizar'}
              </button>
              
              <button
                onClick={() => setShowConfirmation(true)}
                disabled={isLoading || messages.length === 0}
                className="px-4 py-2 bg-gradient-to-r from-red-600 to-red-700 hover:from-red-500 hover:to-red-600 disabled:from-gray-600 disabled:to-gray-700 rounded-lg font-medium transition-all duration-300 transform hover:scale-105 disabled:scale-100"
              >
                {isLoading ? `Deletando... (${deleteCount})` : `Deletar Todas (${messages.length})`}
              </button>
            </div>
          </div>

          {isLoadingMessages ? (
            <div className="text-center py-8">
              <div className="loading-spinner mx-auto mb-4"></div>
              <p className="text-gray-400">Carregando mensagens...</p>
            </div>
          ) : messages.length === 0 ? (
            <div className="text-center py-8 space-y-4">
              <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mx-auto">
                <span className="text-2xl">💬</span>
              </div>
              <p className="text-gray-400">Nenhuma mensagem sua encontrada neste canal</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-96 overflow-y-auto">
              {messages.map((message, index) => (
                <div
                  key={message.id}
                  className="p-4 bg-gray-800/50 border border-gray-700 rounded-lg animate-slide-in-left"
                  style={{ animationDelay: `${index * 0.05}s` }}
                >
                  <div className="flex items-start space-x-3">
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-1">
                        <span className="font-medium text-white">{message.author.username}</span>
                        <span className="text-xs text-gray-400">{formatDate(message.timestamp)}</span>
                      </div>
                      <p className="text-gray-300 text-sm">
                        {message.content || '<Mensagem sem texto>'}
                      </p>
                    </div>
                    <div className="text-xs text-gray-500">
                      {message.id}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Modal de Confirmação */}
      {showConfirmation && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-gray-900 border border-red-600/30 rounded-xl p-6 max-w-md w-full mx-4 space-y-4">
            <div className="text-center space-y-2">
              <div className="w-16 h-16 bg-red-600/20 rounded-full flex items-center justify-center mx-auto">
                <span className="text-3xl">⚠️</span>
              </div>
              <h3 className="text-xl font-bold text-white">Confirmar Exclusão</h3>
              <p className="text-gray-300">
                Tem certeza que deseja deletar todas as {messages.length} mensagens suas neste canal?
              </p>
              <p className="text-red-400 text-sm">
                Esta ação não pode ser desfeita!
              </p>
            </div>
            
            <div className="flex gap-4">
              <button
                onClick={() => setShowConfirmation(false)}
                className="flex-1 px-4 py-2 bg-gray-600 hover:bg-gray-500 rounded-lg font-medium transition-all duration-300"
              >
                Cancelar
              </button>
              <button
                onClick={handleDeleteAllMessages}
                disabled={isLoading}
                className="flex-1 px-4 py-2 bg-red-600 hover:bg-red-500 disabled:bg-gray-600 rounded-lg font-medium transition-all duration-300"
              >
                {isLoading ? 'Deletando...' : 'Confirmar'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Mensagens de Status */}
      {error && (
        <div className="bg-red-900/30 border border-red-600/30 rounded-lg p-4 text-red-300 animate-slide-in-left">
          {error}
        </div>
      )}

      {success && (
        <div className="bg-green-900/30 border border-green-600/30 rounded-lg p-4 text-green-300 animate-slide-in-left">
          {success}
        </div>
      )}

      {/* Aviso */}
      <div className="bg-yellow-900/20 border border-yellow-600/30 rounded-xl p-6 space-y-3">
        <div className="flex items-center space-x-2">
          <span className="text-2xl">⚠️</span>
          <h3 className="text-lg font-semibold text-yellow-300">Importante</h3>
        </div>
        <div className="text-yellow-200 space-y-2 text-sm">
          <p>• Apenas suas próprias mensagens serão deletadas</p>
          <p>• A exclusão é permanente e não pode ser desfeita</p>
          <p>• Há um delay de 1.5 segundos entre cada exclusão para evitar rate limiting</p>
          <p>• Mensagens muito antigas podem não ser carregadas pela API</p>
          <p>• Use com responsabilidade para não prejudicar conversas importantes</p>
        </div>
      </div>
    </div>
  )
}
